	import { mapState, mapMutations } from 'vuex'

	export default {
		computed: {
      ...mapState(['userInfo'])
    },
		methods: {
			...mapMutations(['setUserInfo']),
			getUserInfo(e) {
				this.setUserInfo(e.detail.userInfo)
				uni.setStorageSync('userInfo', this.userInfo)
			},
			goToPage(url) {
				if (!url) {
					return
				}
				uni.navigateTo({
					url
				})
			},
			clearUserInfo() {
				uni.removeStorageSync('userInfo')
				this.setUserInfo({})
			}
		}
	}
